/**
 * Message Queue Implementation
 *
 * High-performance message queue for real-time agent communication
 * with priority handling, batching, and optimization.
 */

export type MessagePriority = "critical" | "high" | "normal" | "low";

export interface Message {
  id: string;
  type: string;
  priority: MessagePriority;
  content: string;
  timestamp: number;
  agentId: string;
  expiresAt?: number;
  retryCount?: number;
  metadata?: Record<string, any>;
}

export interface QueueConfig {
  maxSize: number;
  batchSize: number;
  processingInterval: number;
  priorityLevels: string[];
  enableBatching: boolean;
  enableDeduplication: boolean;
  retryAttempts: number;
  retryDelay: number;
}

export interface QueueMetrics {
  totalMessages: number;
  pendingMessages: number;
  processedMessages: number;
  errorCount: number;
  averageProcessingTime: number;
  throughput: number;
  errorRate: number;
  queueSize: number;
  averageBatchSize: number;
}

export interface MessageProcessor {
  (
    message: Message,
  ): Promise<{ success: boolean; result?: any; error?: string }>;
}

export interface BatchProcessor {
  (
    messages: Message[],
  ): Promise<Array<{ success: boolean; result?: any; error?: string }>>;
}

export interface MessageQueue {
  getConfig(): QueueConfig;
  getMetrics(): QueueMetrics;
  enqueue(message: Message): Promise<void>;
  dequeue(): Promise<Message | null>;
  processNext(): Promise<void>;
  processBatch(): Promise<void>;
  setProcessor(processor: MessageProcessor): void;
  setBatchProcessor(processor: BatchProcessor): void;
  startAutoProcessing(interval?: number): void;
  stopAutoProcessing(): void;
  findMessages(filter: Partial<Message>): Message[];
  searchMessages(query: string): Message[];
  removeExpiredMessages(): Promise<void>;
  compact(): Promise<void>;
  optimizeBatchSize(): Promise<void>;
  getDeadLetterQueue(): Message[];
  performHealthCheck(): Promise<boolean>;
  recover(): Promise<void>;
  simulateCorruption(): void;
  getPerformanceReport(): any;
}

class MessageQueueImpl implements MessageQueue {
  private config: QueueConfig;
  private messages: Map<string, Message> = new Map();
  private priorityQueues: Map<MessagePriority, Message[]> = new Map();
  private deadLetterQueue: Message[] = [];
  private processor?: MessageProcessor;
  private batchProcessor?: BatchProcessor;
  private autoProcessingInterval?: NodeJS.Timeout;
  private metrics: QueueMetrics;
  private processingTimes: number[] = [];
  private batchSizes: number[] = [];
  private isCorrupted = false;

  constructor(config: QueueConfig) {
    if (config.maxSize <= 0) {
      throw new Error("Invalid configuration: maxSize must be positive");
    }

    this.config = config;

    // Initialize priority queues
    (config.priorityLevels as MessagePriority[]).forEach((priority) => {
      this.priorityQueues.set(priority, []);
    });

    this.metrics = {
      totalMessages: 0,
      pendingMessages: 0,
      processedMessages: 0,
      errorCount: 0,
      averageProcessingTime: 0,
      throughput: 0,
      errorRate: 0,
      queueSize: 0,
      averageBatchSize: 0,
    };
  }

  getConfig(): QueueConfig {
    return { ...this.config };
  }

  getMetrics(): QueueMetrics {
    this.updateMetrics();
    return { ...this.metrics };
  }

  async enqueue(message: Message): Promise<void> {
    if (this.messages.size >= this.config.maxSize) {
      throw new Error("Queue full");
    }

    // Deduplication
    if (this.config.enableDeduplication && this.messages.has(message.id)) {
      return; // Skip duplicate
    }

    // Add to messages map
    this.messages.set(message.id, message);

    // Add to priority queue
    const priorityQueue = this.priorityQueues.get(message.priority);
    if (priorityQueue) {
      priorityQueue.push(message);
      // Sort by timestamp for FIFO within priority
      priorityQueue.sort((a, b) => a.timestamp - b.timestamp);
    }

    this.metrics.totalMessages++;
    this.updateMetrics();
  }

  async dequeue(): Promise<Message | null> {
    // Check priority queues in order
    for (const priority of this.config.priorityLevels as MessagePriority[]) {
      const queue = this.priorityQueues.get(priority);
      if (queue && queue.length > 0) {
        const message = queue.shift()!;
        this.messages.delete(message.id);
        this.updateMetrics();
        return message;
      }
    }
    return null;
  }

  async processNext(): Promise<void> {
    if (!this.processor) {
      throw new Error("No processor configured");
    }

    const message = await this.dequeue();
    if (!message) {
      return;
    }

    const startTime = Date.now();

    try {
      const result = await this.processor(message);

      if (result.success) {
        this.metrics.processedMessages++;
      } else {
        this.metrics.errorCount++; // Count errors immediately
        await this.handleProcessingFailure(message);
      }
    } catch (error) {
      this.metrics.errorCount++; // Count errors immediately  
      await this.handleProcessingFailure(message);
    }

    const processingTime = Date.now() - startTime;
    this.processingTimes.push(processingTime);

    // Keep only recent processing times
    if (this.processingTimes.length > 100) {
      this.processingTimes = this.processingTimes.slice(-50);
    }

    this.updateMetrics();
  }

  private async handleProcessingFailure(message: Message): Promise<void> {
    const retryCount = (message.retryCount || 0) + 1;

    if (retryCount <= this.config.retryAttempts) {
      // Retry the message
      message.retryCount = retryCount;
      await this.enqueue(message);
    } else {
      // Move to dead letter queue (error already counted in processNext)
      this.deadLetterQueue.push(message);
    }
  }

  async processBatch(): Promise<void> {
    if (!this.batchProcessor) {
      // Fall back to individual processing
      for (let i = 0; i < this.config.batchSize; i++) {
        await this.processNext();
      }
      return;
    }

    const batch: Message[] = [];

    // Collect batch
    for (let i = 0; i < this.config.batchSize; i++) {
      const message = await this.dequeue();
      if (!message) break;
      batch.push(message);
    }

    if (batch.length === 0) return;

    const startTime = Date.now();

    try {
      const results = await this.batchProcessor(batch);

      results.forEach((result, index) => {
        if (result.success) {
          this.metrics.processedMessages++;
        } else {
          this.handleProcessingFailure(batch[index]);
        }
      });
    } catch (error) {
      // Handle batch failure
      batch.forEach((message) => this.handleProcessingFailure(message));
    }

    const batchTime = Date.now() - startTime;
    this.batchSizes.push(batch.length);
    this.processingTimes.push(batchTime / batch.length); // Average per message

    this.updateMetrics();
  }

  setProcessor(processor: MessageProcessor): void {
    this.processor = processor;
  }

  setBatchProcessor(processor: BatchProcessor): void {
    this.batchProcessor = processor;
  }

  startAutoProcessing(interval?: number): void {
    const processingInterval = interval || this.config.processingInterval;

    this.autoProcessingInterval = setInterval(async () => {
      try {
        if (this.config.enableBatching) {
          await this.processBatch();
        } else {
          await this.processNext();
        }
      } catch (error) {
        console.error("Auto processing error:", error);
      }
    }, processingInterval);
  }

  stopAutoProcessing(): void {
    if (this.autoProcessingInterval) {
      clearInterval(this.autoProcessingInterval);
      this.autoProcessingInterval = undefined;
    }
  }

  findMessages(filter: Partial<Message>): Message[] {
    const results: Message[] = [];

    this.messages.forEach((message) => {
      let matches = true;

      Object.entries(filter).forEach(([key, value]) => {
        if (message[key as keyof Message] !== value) {
          matches = false;
        }
      });

      if (matches) {
        results.push(message);
      }
    });

    return results;
  }

  searchMessages(query: string): Message[] {
    const results: Message[] = [];
    const lowerQuery = query.toLowerCase();

    this.messages.forEach((message) => {
      if (message.content.toLowerCase().includes(lowerQuery)) {
        results.push(message);
      }
    });

    return results;
  }

  async removeExpiredMessages(): Promise<void> {
    const now = Date.now();
    const expiredIds: string[] = [];

    this.messages.forEach((message, id) => {
      if (message.expiresAt && message.expiresAt < now) {
        expiredIds.push(id);
      }
    });

    expiredIds.forEach((id) => {
      const message = this.messages.get(id);
      if (message) {
        this.messages.delete(id);

        // Remove from priority queue
        const priorityQueue = this.priorityQueues.get(message.priority);
        if (priorityQueue) {
          const index = priorityQueue.findIndex((m) => m.id === id);
          if (index >= 0) {
            priorityQueue.splice(index, 1);
          }
        }
      }
    });

    this.updateMetrics();
  }

  async compact(): Promise<void> {
    // Rebuild priority queues from messages map
    this.priorityQueues.forEach((queue) => (queue.length = 0));

    this.messages.forEach((message) => {
      const priorityQueue = this.priorityQueues.get(message.priority);
      if (priorityQueue) {
        priorityQueue.push(message);
      }
    });

    // Sort each priority queue
    this.priorityQueues.forEach((queue) => {
      queue.sort((a, b) => a.timestamp - b.timestamp);
    });

    this.updateMetrics();
  }

  async optimizeBatchSize(): Promise<void> {
    if (this.batchSizes.length < 10) return;

    // Calculate optimal batch size based on performance
    const recentBatches = this.batchSizes.slice(-20);
    const averageBatchSize =
      recentBatches.reduce((a, b) => a + b, 0) / recentBatches.length;

    // Adjust config based on performance metrics
    if (
      this.metrics.averageProcessingTime < 50 &&
      averageBatchSize < this.config.batchSize
    ) {
      this.config.batchSize = Math.min(this.config.batchSize + 5, 50);
    } else if (this.metrics.averageProcessingTime > 200) {
      this.config.batchSize = Math.max(this.config.batchSize - 5, 1);
    }
  }

  getDeadLetterQueue(): Message[] {
    return [...this.deadLetterQueue];
  }

  async performHealthCheck(): Promise<boolean> {
    if (this.isCorrupted) return false;

    // Check queue consistency
    let totalInQueues = 0;
    this.priorityQueues.forEach((queue) => {
      totalInQueues += queue.length;
    });

    return totalInQueues === this.messages.size;
  }

  async recover(): Promise<void> {
    this.isCorrupted = false;

    // Rebuild priority queues
    await this.compact();

    // Reset metrics
    this.metrics.errorCount = 0;
  }

  simulateCorruption(): void {
    this.isCorrupted = true;
  }

  getPerformanceReport(): any {
    const recentProcessingTimes = this.processingTimes.slice(-20);
    const avgTime =
      recentProcessingTimes.reduce((a, b) => a + b, 0) /
      recentProcessingTimes.length;

    // Detect degradation - lowered threshold to be more sensitive to performance issues
    const degradationDetected = avgTime > 30; // More realistic threshold for degradation

    return {
      degradationDetected,
      averageProcessingTime: avgTime,
      recommendations: degradationDetected
        ? ["Consider scaling processing capacity"]
        : [],
    };
  }

  private updateMetrics(): void {
    let totalInQueues = 0;
    this.priorityQueues.forEach((queue) => {
      totalInQueues += queue.length;
    });

    this.metrics.pendingMessages = totalInQueues;
    this.metrics.queueSize = this.messages.size;

    if (this.processingTimes.length > 0) {
      this.metrics.averageProcessingTime =
        this.processingTimes.reduce((a, b) => a + b, 0) /
        this.processingTimes.length;
    }

    if (this.batchSizes.length > 0) {
      this.metrics.averageBatchSize =
        this.batchSizes.reduce((a, b) => a + b, 0) / this.batchSizes.length;
    }

    this.metrics.errorRate =
      this.metrics.totalMessages > 0
        ? this.metrics.errorCount / this.metrics.totalMessages
        : 0;

    // Calculate throughput (messages per second)
    if (this.processingTimes.length > 1) {
      const totalTime = this.processingTimes.reduce((a, b) => a + b, 0);
      this.metrics.throughput =
        (this.metrics.processedMessages * 1000) / totalTime;
    }
  }
}

export function createMessageQueue(config: QueueConfig): MessageQueue {
  return new MessageQueueImpl(config);
}

export async function processMessage(
  message: Message,
  processor: MessageProcessor,
): Promise<{ success: boolean; result?: any; error?: string }> {
  try {
    return await processor(message);
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : "Unknown error",
    };
  }
}

export async function batchProcessMessages(
  messages: Message[],
  processor: MessageProcessor,
): Promise<Array<{ success: boolean; result?: any; error?: string }>> {
  const results = [];

  for (const message of messages) {
    results.push(await processMessage(message, processor));
  }

  return results;
}

export function optimizeQueue(queue: MessageQueue): void {
  // Perform various optimizations
  queue.removeExpiredMessages();
  queue.compact();
  queue.optimizeBatchSize();
}
